import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Offset from '../../../../../../Views/Offset';
import CardApplicationReview from '../../../../../../Views/CardApplicationReview/CardApplicationReview';
import Spinner from '../../../../../../Views/Spinner';
import CardApplicationReviewMembers from '../../../../../../Views/CardApplicationReviewMembers';
import Pagination from '../../../../../../Views/Pagination/Pagination';

const OutWarrantlyMembtrsInfo = (

) => {

    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg } = useStoreon('tg');
    const [dataUser, setDataUser] = useState({});
    const [isLoadingInfo, setIsLoadingInfo] = useState(false);
    const [data, setData] = useState({});

    useEffect(() => {
        dispatch('setTextHeader', `${state?.dataUser?.user_data?.user_profile_name ?? ''}`);
        dispatch('setActiveHeaderText', true);
        tg.BackButton.show();
        tg.BackButton.onClick(() => navigate('/button_rating/button_garant_rating/button_garant_members', {
            state: {
                name_button: state?.old_name_button ?? '',
                city_id: state.city_id
            }
        }))
    }, [dispatch, tg.BackButton, navigate, state?.old_name_button, state?.city_id]);


    useEffect(() => {
        if (state?.dataUser) {
            setDataUser(state.dataUser)
        }
    }, [])
    useEffect(() => {

        let params = {
            url: '/api_get_city_member_info/',
            user_id: state.dataUser.id,
            page_size: 30,
            setIsLoading: setIsLoadingInfo,
            dataRequst: res => {
                const copyData = res?.result?.results?.slice();
                setData(c => ({
                    ...res.result,
                    results: [...copyData]
                }));
            }
        }
        dispatch('getDataContent', params)
    }, [])

    const changePaginationPage = data => {
        const { page } = data;
        const paramsList = {
            url: '/api_get_city_member_info/',
            user_id: dataUser.id,
            page: page ?? 1,
            page_size: 30,
            setIsLoading: setIsLoadingInfo,
            dataRequst: res => {
                const copyData = res?.result?.results?.slice();
                setData(c => ({
                    ...res.result,
                    results: [...copyData]
                }));
            },
        };
        dispatch('setGetContext', paramsList);
    }

    return (
        <React.Fragment>
            <CardApplicationReviewMembers
                listAplication={{ results: [dataUser] }}
            />

            {
                isLoadingInfo ?
                    <Spinner />
                    : data?.count ?
                        <React.Fragment>
                            <div> {data?.count} ()</div>
                            <CardApplicationReview
                                modal
                                listAplication={data}
                                tg={tg}
                            />

                        </React.Fragment>
                        : null
            }
            {
                data?.count > 30 ?
                    <Pagination
                        allCount={data.count}
                        count={30}
                        handlerChangePaginations={changePaginationPage}
                        currentPage={Number(data.current_page)}
                        location={'center'}
                    />
                    : null
            }
            <Offset mb={50} />
        </React.Fragment>
    )
}
export default OutWarrantlyMembtrsInfo;